
header{
  text-align: center;
}

.nav-link{
  margin-right:100px !important ;
}

.products{
  display: flex;
  flex-wrap: wrap;
  background-color: #eee;
  margin-left: 5px !important;
 
}

.Item{
  background-color: rgb(185, 201, 44);
  width: 300px !important;
  margin: 4% !important;
}

.Item-content{
  font-size: 15px !important;
}

.Item-btn{
  height: 35px !important;
  margin-bottom:5px !important ;
}

 .card-more{
   position: relative;
   top:0;
 }

 ::placeholder {
  color: black;
  opacity: 1; /* Firefox */
}

:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: black;
 }
 
 ::-ms-input-placeholder { /* Microsoft Edge */
  color: black;
 }

 .email{
  border:1px solid black !important;
  height: 30px !important;
 }

 .address{
  border:1px solid black !important;
  height: 90px !important;
 }
 
 .radio-btn{
   margin-right: 30px;
 }

 .navbar{
  width:auto;
  height:0.5%;
  border: 1px solid black;
  border-radius:6px;
  box-shadow: 4px 3px gray;
  margin:2px; 
  position: sticky;
  top: 0;
  /* transition-timing-function: ease-in;
  transition: 0.5 */
}

.nav-wrapper{
  padding-left: 10px;
}

.nav-link:hover ul{
  display: block ;
}
.nav-li{
  margin-left: 14px;
}

.category{
  margin-left: 5px !important;
  z-index: 1;
  background-color: beige;
}

.logo{
  width: 80px!important;
}
 /* For parallax effect on both small and big screen */
.parallax02{
  height: 160px;
  margin-left: 0.5%;
  margin-right: 0.5%;
}
@media only screen and (max-width: 600px) {
  .parallax-container{
   height: 50px;
  }
}


@media only screen and (max-width: 800px) {
  nav img{
    position: relative;
    top: 0px;
    left: 60%;
    border-radius: 8px;
    border: 1px solid black;
  }
}

li div {
